import React from 'react'
import { Row, Table } from 'reactstrap'
import '../../assets/css/BillForm.css'

const Invoice = () => {
    return (
        <React.Fragment>
            <div className="page-content">
                <Row>
                    <div className="col-12">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                            <h4 className="page-title mb-0 font-size-18">Invoice</h4>

                            <div className="page-title-right">
                                <ol className="breadcrumb m-0">
                                    <li className="breadcrumb-item active">
                                        Welcome to Fitsol Dashboard
                                    </li>
                                </ol>
                            </div>
                        </div>
                    </div>
                </Row>
                <div style={{ backgroundColor: 'white' }}>
                    <Table bordered style={{ width: '100%' }}>
                        <tbody>
                            <tr>
                                <th colspan={3} rowspan={2}>1,2,3</th>
                                <td colspan={2}>4,5</td>
                                <td colspan={3} rowspan={2}>6,7,8</td>
                            </tr>
                            <tr>
                                <td colspan={2}>4,5</td>
                            </tr>
                            <tr>
                                <td className='primaryColor'>Branch Name</td>
                                <td className='primaryColor'>From</td>
                                <td className='primaryColor'>To</td>
                                <td colspan={2}>Dated : 4,5</td>
                                <td colspan={3}>Vehicle No : 6,7,8</td>
                            </tr>
                            <tr>
                                <td>1</td>
                                <td>2</td>
                                <td>3</td>
                                <td colspan={2}>OWNER RISK</td>
                                <td colspan={3} className='primaryColor'>MODE</td>
                            </tr>
                            <tr>
                                <td colspan={3} className='primaryColor'>CONSIGNOR</td>
                                <td colspan={2} className='primaryColor'>CONSIGNEE</td>
                                <td>ROAD</td>
                                <td>TRAIN</td>
                                <td>AIR</td>
                            </tr>
                            <tr>
                                <td>1</td>
                                <td>2</td>
                                <td>3</td>
                                <td colspan={2}>Dated :</td>
                                <td>Vehicle No. :</td>
                            </tr>
                        </tbody>
                    </Table>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Invoice